<template>
  <div class="hello">
    <select v-model="selectedCountry">
      <option v-for="c in countries" :key="c.Country" :value="c.Country">
        {{ c.Country }}
      </option>
    </select>
    <button @click="getAllCases()">Ver Casos</button>
<div v-if="covid != undefined">
    <div v-if="covid.length != 0">
      <p>
        <b>COUNTRY:</b>
        {{ this.covid.Country }}
      </p>
      <p>
        <b>DATE:</b>
        {{ this.covid.Date }}
      </p>
      <p>
        <b>CONFIRMED:</b>
        {{ this.covid.NewConfirmed }} of {{ this.covid.TotalConfirmed }}
      </p>
      <p>
        <b>DEATHS:</b>
        {{ this.covid.NewDeaths }} of {{ this.covid.TotalDeaths }}
      </p>
      <p>
        <b>RECOVERD:</b>
        {{ this.covid.NewRecovered }} of {{ this.covid.TotalRecovered }}
      </p>
    </div>
</div>
    <div v-if="covid == undefined">
      <p><b>Não</b> existem dados para este país!</p>
    </div>
  </div>
</template>

<script>
export default {
  name: "covid19",
  data() {
    return {
      covid: [],
      countries: [],
      selectedCountry: "Portugal"
    };
  },
  async created() {
    this.getAllCountries();
    this.getAllCases();
  },

  methods: {
    async getAllCountries() {
      const response = await fetch("https://api.covid19api.com/countries");
      const myData = await response.json();
      this.countries = myData;
    },
    async getAllCases() {
      const response = await fetch("https://api.covid19api.com/summary");
      const myData = await response.json();
      this.covid = myData.Countries.find(
        country => country.Country == this.selectedCountry
      );
    }
  }
};
</script>

<style scoped>
b {
  color: #fc4c7c;
}
button {
  margin-left: 10px;
}
</style>
